<template>
	<div class="newCustomer-page input-border-none self-mt-header self-vant-file" v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%">
        <header class="post-head" v-show='stepOne'>
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">企业认证—基本信息</span>
        </header>
        <header class="post-head" v-show='stepTwo'>
            <a href="javascript:;" class="back" @click="imgBackList"></a> 
            <span class="head-font">企业认证—附件信息</span>
        </header>
        <header class="post-head" v-show='stepThree'>
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">企业认证—等待审核</span>
        </header>
        <header class="post-head" v-show='showTextarea'>
            <a href="javascript:;" class="back" @click="textareaBackList"></a> 
            <span class="head-font">企业简介</span>
        </header>

		<!-- <mt-header title="企业认证-基本信息" fixed v-show='stepOne'>
            <router-link to="/qdsPersonal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
		<!-- <mt-header title="企业认证-等待审核" fixed v-show='stepThree'>
            <router-link to="/qdsPersonal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
		<!-- <mt-header title="企业认证-图片信息" fixed v-show='stepTwo'>
            <mt-button slot="left" icon="back" @click="imgBackList"></mt-button>
        </mt-header> -->
		<!-- <mt-header title="企业简介" fixed v-show='showTextarea'>
            <mt-button slot="left" icon="back" @click="textareaBackList"></mt-button>
        </mt-header> -->
        <div class="zhanwei"></div>
        <div class="infor-write" v-show="!showTextarea">
            <div v-show='stepOne'>
                <!-- <el-form id="addCompany" :model="ruleCompanyAuth" ref="ruleCompanyAuth" label-width="80px" class="demo-ruleForm">
                    <el-form-item label="企业全称" prop="companyAllName">
                        <el-select
                        class="test"
                            :disabled='disabled.companyAllName'
                            v-model.trim="ruleCompanyAuth.companyAllName"
                            filterable
                            remote
                            placeholder="请填写企业名称"
                            :remote-method="getcompanyName"
                            :automatic-dropdown ="true"
                            :loading="loading"
                            >
                                <el-option
                                    v-if="companyNameList"
                                    v-for="item in companyNameList"
                                    @click.native="selectCompanyName(item.value, item.label)"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-select>
                    </el-form-item>
                </el-form> -->
                <van-cell-group class="cell-self" > 
                    <div class="item-file item-file-one">
                        <van-field v-model.trim="ruleCompanyAuth.companyAllName" :disabled='disabled.companyAllName'  clearable label="企业全称"  placeholder="请填写企业名称" @input="getcompanyName"/>
                        <div class="mask" v-show="companyAllNameToggle" @click="companyNameClose"></div>
                        <div class="el-select-dropdown" v-if="ruleCompanyAuth.companyAllName" v-show="companyAllNameToggle">
                            <div class="el-scrollbar">
                                <div class="el-select-dropdown__wrap el-scrollbar__wrap">
                                    <ul class="el-select-dropdown__list">
                                        <li class="el-select-dropdown__item" v-for="(item,index) in companyNameList" :key="index" @click="selectCompanyName(item.value, item.label)">{{item.label}}</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>      
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.companyTitle" maxlength="6"  :disabled='disabled.companyTitle'  clearable label="企业简称"  placeholder="请输入企业简称" />
                    </div>         
                    <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.companyCode"  :disabled='disabled.companyCode' @blur="companyCodeBlur"  clearable label="社会信用代码"  placeholder="请输入统一社会信用代码" />
                    </div>         
                    <div class="item-file">
                        <div class="item-file-child"  @click='selectCity'></div>
                        <van-field v-model.trim="ruleCompanyAuth.selecteCity"  disabled  clearable label="企业地址" right-icon="arrow" placeholder="请输入企业地址" @click-right-icon="selectCity" />
                    </div>
                     <div class="item-file">
                         <van-field v-model.trim="ruleCompanyAuth.companyAddress"  :disabled='disabled.companyAddress' @focus="companyAddressFocus"  @blur="companyAddressBlur"  clearable label="详细地址" placeholder="请输入详细地址" />
                    </div>   
                    <div class="item-file">
                        <div class="item-file-child" @click='selectScale'></div>
                       <van-field v-model.trim="ruleCompanyAuth.companysSize"  disabled  clearable label="企业规模" right-icon="arrow" placeholder="请选择企业规模" @click-right-icon="selectScale" />
                    </div>   
                    <div class="item-file">
                        <div class="item-file-child" @click='selectNature'></div>
                       <van-field v-model.trim="ruleCompanyAuth.companysNature"   disabled clearable label="企业性质" right-icon="arrow" placeholder="请选择企业性质" @click-right-icon="selectNature" />
                    </div>   
                    <div class="item-file">
                        <div class="item-file-child" @click='selectIndustry'></div>
                        <van-field v-model.trim="ruleCompanyAuth.companyIndustry"  disabled clearable label="所属行业" right-icon="arrow" placeholder="其选择所属行业" @click-right-icon="selectIndustry" />
                    </div>   
                    <div class="item-file">
                        <div class="item-file-child" @click='getTextarea'></div>
                        <van-field   disabled clearable label="企业简介" v-model.trim="ruleCompanyAuth.companyWorkCont" right-icon="arrow" placeholder="企业简介" @click-right-icon="getTextarea" />
                    </div>   
                    <div class='zhanwei-bottom' v-show="showMove"></div>
                </van-cell-group>
                
                <div class="next-one" @click="nextOne">
                    <p>下一步</p>
                </div>
            </div>
            <div v-show="stepTwo" class="stepTwo">
                <div class="img-item">
                    <p>营业执照</p>
                    <div>
                        <input type="file" id="businessLicenseImgId" class="hidden-input" :disabled='disabled.businessLicenseImg' :value="imgValue" @change="uploadImgPhone($event, 1)" accept="image/*">
                        <img :src="ruleCompanyAuth.businessLicenseImg" alt="" v-if="ruleCompanyAuth.businessLicenseImg != ''" @click="getPreview(businessLicenseImg, 1)">
                        <label for="businessLicenseImgId" class="label-logo"><img :src="uploadImg" alt="" v-show="businessLicenseImg.length === 0"></label>
                    </div>
                    
                </div>
                <!-- <div class="img-item">
                    <p>企业封面图</p>
                    <div>
                        <input type="file" id="surfacePlotImgId" class="hidden-input" :disabled='disabled.surfacePlotImg' :value="imgValue" @change="uploadImgPhone($event, 1)" accept="image/*">
                        <img :src="ruleCompanyAuth.surfacePlotImg" alt="" v-if="ruleCompanyAuth.surfacePlotImg != ''">
                        <label for="surfacePlotImgId" class="label-logo"><img :src="uploadImg" alt=""></label>
                    </div>
                </div>
                <div class="img-item">
                    <p>企业实景图</p>
                    <div>
                        <input type="file" id="liveActionsImgId" class="hidden-input" :disabled='disabled.firstOrder' :value="imgValue" @change="uploadImgPhone($event, 1)" accept="image/*">                        
                        <img :src="ruleCompanyAuth.liveActionsImg[0]" v-if="ruleCompanyAuth.liveActionsImg.length > 0" alt="" @click="getPreview(ruleCompanyAuth.liveActionsImg)" :class="ruleCompanyAuth.liveActionsImg.length > 1 ? 'shadow-img' : ''">
                        <label for="liveActionsImgId" class="label-logo"><img :src="uploadImg" alt=""></label>
                    </div>
                </div> -->
                <div class="img-item" >
                    <p>一手招聘订单</p>
                    <div>
                        <input type="file" id="orderId" class="hidden-input" :disabled='disabled.liveActionsImg' :value="imgValue" @change="uploadImgPhone($event, 2)">                        
                        <img :src="ruleCompanyAuth.firstOrder[0]" v-if="ruleCompanyAuth.firstOrder.length > 0" alt="" @click="getPreview(ruleCompanyAuth.firstOrder, 2)" :class="ruleCompanyAuth.firstOrder.length > 1 ? 'shadow-img' : ''">
                        <div class="file-box" v-show="showFile">
                            <img :src="textBg" alt="" >
                            <span class="file-tips">客户合作协议为文件</span>
                            <img :src="delImg" alt="" class="del-file-img" @click="delFile">
                        </div>
                      
                        <label for="orderId" class="label-logo" v-show="!showFile"><img :src="uploadImg" alt=""></label>
                    </div>
                </div>
                <div class="imgs-box-tips" v-show="ruleCompanyAuth.firstOrder.length < 1 && !showFile">
                    <p>仅支持上传图片</p>
                    <p>可以上传多张</p>
                </div>
                <div class="next-one" @click="stepThreeNext" >
                    <p>提交验证</p>
                </div>
            </div>
        </div>
        <div v-show="showTextarea" class="text-box">
            <div class="zhanwei-top"></div>
            <mt-field placeholder="请填写企业简介" :disabled='disabled.companyWorkCont' type="textarea" rows="4" v-model.trim="ruleCompanyAuth.companyWorkCont" class="text_area"></mt-field>
            <div class="textarea-tips"><p>不要填写电话、QQ、微信等联系方式</p></div>
            <div class="next-text">
                <p @click="textareaBackListBtn">完成</p>
            </div>
        </div>
        <div v-show="stepThree" class="step-three" :style="clientHeight">
            <div class="cont">
                <img :src="successImg" alt="">
                <p>您已成功提交验证，请耐心等待审核</p>
                <!-- <p>如果审核通过，我们会通过短信告知</p> -->
                <p>如果超过3天没有审核通过，</p>
                <p>请联系我们的客服021-22876396</p>
            </div>
        </div>
        <!--企业规模 企业性质  所属行业 省市区的picker-->
        <van-picker class="fixed-pop" v-show="showScale" :columns="scaleColumns"  show-toolbar title="企业规模"  @cancel="scaleCancel" @confirm="scaleConfirm"/>
        <van-picker class="fixed-pop" v-show="showNature" :columns="natureColumns"  show-toolbar title="企业性质"  @cancel="natureCancel" @confirm="natureConfirm"/>
        <van-picker class="fixed-pop" v-show="showIndustry" :columns="industryColumns" show-toolbar title="所属行业"  @cancel="industryCancel" @confirm="industryConfirm"/>
		<van-popup v-model="showPop" position="bottom " :overlay="false">
			<van-area v-if="showArea" :area-list="areaList" value="110101" @confirm='confirmArea' @cancel='cancelArea'/>
		</van-popup>
        <!--  -->
        <van-icon name="delete"  class="delete" @click="deleteLiveActionsImg" v-show="delIcon"/>

        <!-- 图片裁剪上传 -->
        <div class="welfare-popup cut-img-box" v-show="showCutImg" :style="clientHeight">
            <div class="welfare-title">
                <div class="van-picker__cancel" @click="falseCutImg">取消</div>
                <div class="van-picker__confirm" @click="trueCutImg">确认</div>
            </div>
            <!-- img	裁剪图片的地址	空	url 地址 || base64 || blob
                outputSize	裁剪生成图片的质量	1	0.1 - 1
                outputType	裁剪生成图片的格式	jpg (jpg 需要传入jpeg)	jpeg || png || webp
                info	裁剪框的大小信息	true	true || false
                canScale	图片是否允许滚轮缩放	true	true || false
                autoCrop	是否默认生成截图框	false	true || false
                autoCropWidth	默认生成截图框宽度	容器的80%	0~max
                autoCropHeight	默认生成截图框高度	容器的80%	0~max
                fixed	是否开启截图框宽高固定比例	true	true | false
                fixedNumber	截图框的宽高比例	[1 : 1]	[宽度 : 高度]
                full	是否输出原图比例的截图	false	true | false
                fixedBox	固定截图框大小 不允许改变	false	true | false
                canMove	上传图片是否可以移动	true	true | false
                canMoveBox	截图框能否拖动	true	true | false
                original	上传图片按照原始比例渲染	false	true | false
                centerBox	截图框是否被限制在图片里面	false	true | false
                high	是否按照设备的dpr 输出等比例图片	true	true | false
                infoTrue	true 为展示真实输出图片宽高 false 展示看到的截图框宽高	false	true | false
                maxImgSize	限制图片最大宽度和高度	2000	0-max -->
            <div class="cropper">
                <vue-cropper 
                    ref="cropper" 
                    :img="option.img" 
                    :outputSize="option.size" 
                    :outputType="option.outputType" 
                    :info="true" 
                    :full="option.full" 
                    :fixedNumber="option.fixedNumber"
                    :fixed="option.fixed"
                    :canMove="option.canMove" 
                    :canMoveBox="option.canMoveBox" 
                    :original="option.original" 
                    :centerBox="option.centerBox" 
                    :autoCrop="option.autoCrop" 
                    :autoCropWidth="option.autoCropWidth" 
                    :autoCropHeight="option.autoCropHeight" 
                    :fixedBox="option.fixedBox"
                    @imgLoad="imgLoad"> 
                </vue-cropper>
            </div>
        </div>
        <div class="mongolia" v-show="mongolia"></div>
	</div>
  
</template>
<script>
import Util from '@/js/mUtils';
import uploadImg from "@/assets/qds-img/upload.png";
import delImg from "@/assets/qds-img/del-img.png";
import successImg from "@/assets/qds-img/new-customer-check.png";
import { ImagePreview, Notify} from 'vant';
import { MessageBox } from 'mint-ui';
import textBg from "@/assets/qds-img/text_bg.jpg";
import {DLDict,DLArea,DLTypeList,DLNatureList,DLSizeList} from '@/js/dl-code';
import { VueCropper } from "vue-cropper"; // 图片裁剪
import OSS from 'ali-oss';
export default {
	data() {
		return{
			globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            uploadImg: uploadImg,
            successImg: successImg,
            delImg: delImg,
            textBg: textBg,
            stepOne:true,
            stepTwo:false,
            stepThree:false,
            loading: false,
            delIcon: false,
			ruleCompanyAuth:{
                companyAllName: '',  // 企业全称
                companyTitle: '',   // 企业简称
                companyCode: '',     // 统一社会信用代码
                selecteCity:'',
                companyAddress:'',   // 详细地址
                companysSize:'',    // 企业规模
                companysNature:'',    // 企业性质
                companyIndustry:'',    // 所属行业
                companyWorkCont:'',   // 企业简介
                businessLicenseImg:'',   // 营业执照
                companyLogo: 'http://ylsite.oss-cn-hangzhou.aliyuncs.com/ylkz/logoImg/2019/1/24/18723_1548317220000.png',
                surfacePlotImg:'http://ylsite.oss-cn-hangzhou.aliyuncs.com/1080/surfacePlotImg/2019/1/24/18723_1548317267000.png',
                liveActionsImg: ['http://ylsite.oss-cn-hangzhou.aliyuncs.com/1080/liveActionsImg/2019/1/24/18723_1548317296000.png'],
                firstOrder:[]       //   一手订单/与客户合作协议图片
            },
            disabled: {         // 如果该企业在该企业库中存在, 则后端返回企业信息, 可能存在空字段, 空字段可以修改, 不为空则不可修改
                companyAllName: false,
                companyTitle: false,
                companyCode: false,
                selecteCity: false,
                companyAddress: false,
                companysSize: false,
                companysNature: false,
                companyIndustry: false,
                leadPhone: false,
                companyWorkCont: false,
                businessLicenseImg: false,
                companyLogo: false,
                surfacePlotImg: false,
                liveActionsImg: false,
                firstOrder: false,
            },
            deltoggle: 1,  // 判断删除哪一个图片
            businessLicenseImg: [],    // 营业执照数组形式
            selecteCity: [],                //企业地址list
            selecteCityShowLenght: [],   // 企业地址
            chanceScaleCode: '',   // 企业规模code
            chanceNatureCode: '',   // 企业性质 code
            chanceIndustryCode: '',  // 所属行业code

            scaleColumns: [], // 企业规模list
            natureColumns: [], // 企业性质list
            industryColumns: [], // 所属行业list
            showScale: false,
            showNature: false,
            showIndustry: false,

            isThereEnterprise: false,    // 通过统一社会信用代码, 查询是否有该企业
            formData: {},  // 传给后端的值
            slots: [],
            companyNameList: [],    // 搜索到的公司集合
			areaList: {
				province_list: {
                   
                },
                city_list: {
                  
                },
                county_list: {
                   
                }
			},
            
			cityArr: [],
			showArea: false,
            showPop: false,
            
            
            showTextarea: false,
            introduction:'',
            qdsAuthState: '',  // 认证状态 
            // 图片裁剪
            showCutImg: false,
            option: {   // 图片裁剪参数
                img:"",
                size: 1,
                full: true,
                fixed: true,
                fixedNumber:[4, 3],    //默认宽高比为4:3
                outputType: "png",
                canMove: true,
                original: false,
                centerBox: true,
                canMoveBox: false,
                autoCrop: true,
                autoCropWidth: 300,
                autoCropHeight: 225,
                fixedBox: true,
                downImg: "#"
            },
            imgValue:'',  // 裁剪图片的value
            oss:{
                region: '',
                accessKeyId: '',
                securityToken: '',
                accessKeySecret: '',
                bucket: ''
            }, 
            clientHeight: {
				height: ""
			},
            closeImg: false,    // 是否异步关闭图片预览
            companyAllNameToggle: false,        //天眼查搜索弹窗
            mongolia: false,
            showFile: false,   // 为true时, 显示为文件, 并且不支持修改
            firstOrderFile: '',   // 存储类型为 文件时的地址
            showMove: false,
		}
	},
	mounted() {
        
        this.qdsAuthState = sessionStorage.getItem('qdsMCertificationStatus')             //认证状态数字 
        if(this.qdsAuthState == 4) {  // 认证失败
            this.recertification()  
        }else if(this.qdsAuthState == 2) {
            this.stepOne = false
            this.stepTwo = false
            this.stepThree = true
        }
		this.getBasicData()   // 基础数据 映射
        this.getCityDate()  // 城市基础数据
        this.getOssParams()                     //获取oss相关数据
	},
	components: {
        VueCropper
    },
    created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
	methods: {
        delFile() {
            this.firstOrderFile = '';
            this.showFile = false;
        },
        getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
         deleteLiveActionsImg() { // 删除 企业实景图片
            var index = document.getElementsByClassName("van-image-preview__index")[0].innerText;
            var currentIndex = index.split('/')[0] - 1
            var total = index.split('/')[1]
            if(total == 1 || (currentIndex+1 == total) ) {
                this.closeImg = true
            }
            if(this.deltoggle == 2) {
                this.ruleCompanyAuth.firstOrder.splice(currentIndex,1)
                this.getPreview(this.ruleCompanyAuth.firstOrder,2)
            }else {
                this.businessLicenseImg.splice(currentIndex,1)
                this.ruleCompanyAuth.businessLicenseImg = ''
                this.getPreview(this.businessLicenseImg,1)
            }
           
        },
        textareaBackList() {
            this.showTextarea = false  // 关闭 textarea页面
            this.stepOne = true        // 显示 基础信息
        },
        textareaBackListBtn() {
             if(this.ruleCompanyAuth.companyWorkCont.length == 0 || this.ruleCompanyAuth.companyWorkCont.length < 100) {
                Notify({
					message: "企业简介字数不少于100个字",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
            this.showTextarea = false  // 关闭 textarea页面
            this.stepOne = true        // 显示 基础信息
        },
         backHistory() {
            window.history.back(-1)
        },
        imgBackList() {
            this.stepOne = true
            this.stepTwo = false
        },
        getBasicData() {
            let that = this
            DLSizeList.forEach(item => { // 企业规模
                this.scaleColumns.push(item.name)
            })
            DLNatureList.forEach(item => {  // 企业性质
                this.natureColumns.push(item.value)
            })
            DLTypeList.forEach(item => {  // 所属行业
                this.industryColumns.push(item.name)
            })
        },
        getPreview(imgArr, num) {   // 预览图片
            var that = this
            that.delIcon = true;
            that.deltoggle = num;
            const instance = ImagePreview({
                images:imgArr,
                asyncClose: that.closeImg,    // 是否 异步关闭
                showIndex:true,
                showIndicators:true,
                loop:false,
                className:'del',
                onClose() {
                    that.closeImg = false
                    that.delIcon = false;
                }
            })
            if(that.closeImg) {
                setTimeout(() => {
                    instance.close();
                }, 10);
            }
        },
        selectCity() {  //显示城市列表
            if(this.disabled.selecteCity){
                return false
            }else {
                this.mongolia = true
                this.showPop = true
                this.showNature = false
                this.showScale = false
                this.showIndustry = false
            }
			
		},
		confirmArea(value) {  // 确定选择省市区
            this.provinceVal = value[0].code
            this.cityVal = value[1].code
            this.countyVal = value[2].code
            this.selecteCityShowLenght = value
            this.ruleCompanyAuth.selecteCity = value[0].name + '/' +value[1].name + '/' +value[2].name
            this.showPop = false
            this.mongolia = false
		},
		cancelArea(value) {  // 取消选择省市区
            this.showPop = false
            this.mongolia = false
		},
        selectScale() {   // 显示企业规模列表
            if(this.disabled.companysSize){
                return false
            }else {
                this.mongolia = true
                this.showScale = true
                this.showPop = false
                this.showNature = false
                this.showIndustry = false
            }
            
        },
        scaleConfirm(value, index) {   // 确定选择企业规模
            let that = this
