        companyCodeBlur(){          //统一社会信用代码
                var that = this
                if(that.ruleCompanyAuth.companyCode == '') {            //企业注册码
                   Notify({
                        message: "请填写营业执照上的注册码",
                        duration: 2000,
                        background: '#E6A23C'
                    });
                    return false
                }else if(!(/^[A-Z0-9]+$/.test(that.ruleCompanyAuth.companyCode))) {
                    Notify({
                        message: "注册码由数字或大写字母组成",
                        duration: 2000,
                        background: '#E6A23C'
                    });
                    return false
                }else {
                    that.repeatCheck()
                }
        },
        repeatCheck() {      // 判断新增企业是否已经改渠道商的客户了,或者已经是渠道商
                var that = this;
                var token = Util.getCookie('qdsMToken');
                that.axios(that.qdsGlobalApiUrl + 'ylw/qhChannelCorp/api/customerQualificatory?token=' + token, {  // 判断新增企业是否已经改渠道商的客户了,或者已经是渠道商
                    method: 'get',
                    params: {
                        creditCode: that.ruleCompanyAuth.companyCode
                    }
                }).then(function(response){
                    if(response.data.code == 82000) {
                        that.ruleCompanyAuth.companyCode = ''  
                        that.ruleCompanyAuth.companyAllName = ''
                        MessageBox({
                            title: '提示',
                            message: '该企业为渠道商，不允许新增为客户， 若有疑问请联系客服021-22876396',
                            showCancelButton: true
                        }).then(action => {
                        }).catch(error => {
                        })
                    }else if(response.data.code == 80001) {
                        that.ruleCompanyAuth.companyCode = ''  
                        that.ruleCompanyAuth.companyAllName = ''
                        MessageBox({
                            title: '提示',
                            message: '该企业为渠道商，不允许新增为客户， 若有疑问请联系客服021-22876396',
                            showCancelButton: true
                        }).then(action => {
                        }).catch(error => {
                        })
                    }else if(response.data.code == 10000){
                        that.byCodeGetInfor()
                        return true
                    }else {
                        Notify({
                            message: response.data.message,
                            duration: 3000,
                            background: '#FF6262'
                        });
                    }

                }).catch(function(error) {

                })
                
        },
        nextOne() {
            let that = this
            var Expression = /^[a-zA-Z\u4E00-\u9FA5]{1,6}$/;   
            var objExp=new RegExp(Expression);
            if(that.ruleCompanyAuth.companyAllName.length == 0) {
                Notify({
					message: "请填写企业全称",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
            if(that.ruleCompanyAuth.companyTitle.length == 0 || !objExp.test(that.ruleCompanyAuth.companyTitle)) {
                Notify({
					message: "请填写企业简称,仅支持填写6位及以下汉字或字母",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }   
            if(that.ruleCompanyAuth.companyCode == '') {
                Notify({
					message: "请填写营业执照上的注册码",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }else if(!(/^[A-Z0-9]+$/.test(that.ruleCompanyAuth.companyCode))) {
                Notify({
					message: "注册码由数字或大写字母组成",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            } 
             if(that.selecteCityShowLenght.length != 3) {
                Notify({
					message: "请选择企业地址",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
            if(that.ruleCompanyAuth.companyAddress == '') {
                Notify({
					message: "请输入企业详细地址",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
             if(that.chanceScaleCode == '') {
                Notify({
					message: "请选择企业规模",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
            if(that.chanceNatureCode == '') {
                Notify({
					message: "请选择企业性质",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
            if(that.chanceIndustryCode == '') {
                Notify({
					message: "请选择所属行业",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
           
           
            if(that.ruleCompanyAuth.companyWorkCont.length == 0 || that.ruleCompanyAuth.companyWorkCont.length < 100) {
                Notify({
					message: "企业简介字数不少于100个字",
					duration: 2000,
					background: '#E6A23C'
                });
                return false
            }
            that.stepOne = false
            that.stepTwo = true
        },
        getCityDate() { // 城市 数据
				var token = Util.getCookie('qdsMToken');
                let that = this;
                that.axios(that.globalApiUrl + "api/recruitManager/findAllCity?token=" +token,{
                    method: "get",
                    params: {
                        type:1,
                    }
                })
                .then(function(response) {
                    if (response.data && response.data.length > 0) {
                        var _resArr = response.data;
                            _resArr.forEach((item, index) => {
                            //用item操作每一条数据。
                            var child = {
                                value: item.code,
                                label: item.name,
                                children:[]
							};  
                            var provinceObj = {
                                [item.code]: item.name,
							};  
							
							Object.assign(that.areaList.province_list, provinceObj)
                            that.cityArr.push(child);
                            var _resArr2 = _resArr[index].childrenList;
                            _resArr2.forEach((item2, index2) => {
                                //用item操作每一条数据。
                                var child2 = {
                                    value: item2.code,
                                    label: item2.name,
                                    children:[]
								};  
								var cityObj = {
										[item2.code]: item2.name,
									};  
								Object.assign(that.areaList.city_list, cityObj)
                                that.cityArr[index].children.push(child2);
                                var _resArr3 = _resArr2[index2].childrenList;
                                _resArr3.forEach((item3, index3) => {
                                    //用item操作每一条数据。
                                    var child3 = {
                                        value: item3.code,
                                        label: item3.name,
									}; 
									var countyObj = {
											[item3.code]: item3.name,
										};  
									Object.assign(that.areaList.county_list, countyObj)
                                    that.cityArr[index].children[index2].children.push(child3);
                                });
                                
                            });
                            
						});
						that.showArea = true;
                    }
                })
                .catch(function(error) {
                });
        },
