                            });
                            if(that.companyNameList.length == 0) {
                                that.disabled.companyCode = false
                            }
                            that.companyNameList = that.list
                                that.companyAllNameToggle = true
                            that.loading = false;
                        }else{
                            that.companyNameList = []
                            that.disabled.companyCode = false
                            that.loading = false;
                        }              
                    })
                    .catch(function (error) {
                        that.loading = false;
                        that.disabled.companyCode = false
                    });
                }, 1000);
            } else {
                that.companyNameList = [];
            }
        },
