        trueUpdata() {    // 确定企业更新  赋值,有内容的不可更改
                var that = this;
                var checkData = that.companyInforData.info;
                if ([null, undefined, ''].indexOf(checkData.name) == -1) {  // 企业全称
                    that.ruleCompanyAuth.companyAllName = checkData.name
                    that.disabled.companyAllName = false;
                }else {
                    that.ruleCompanyAuth.companyAllName = ''
                    
                }
                if ([null, undefined, ''].indexOf(checkData.alias) == -1) {    // 企业简称
                    that.ruleCompanyAuth.companyTitle = checkData.alias
                    that.disabled.companyTitle = true; 
                }else {
                    that.ruleCompanyAuth.companyTitle = ''
                    that.disabled.companyTitle = false; 
                }
                if ([null, undefined, ''].indexOf(checkData.creditCode) == -1) {    // 统一社会信用代码
                    that.ruleCompanyAuth.companyCode = checkData.creditCode
                    that.disabled.companyCode = true;
                }
                if ([null, undefined, ''].indexOf(checkData.provinceCode) == -1 || [null, undefined, ''].indexOf(checkData.cityCode) == -1 || [null, undefined, ''].indexOf(checkData.countyCode) == -1 ) {     // 三级城市provinceCode cityCode countyCode
                    var province = DLArea.getName(checkData.provinceCode)
                    var city = DLArea.getName(checkData.cityCode)
                    var county = DLArea.getName(checkData.countyCode)
                    that.selecteCity = [checkData.provinceCode, checkData.cityCode, checkData.countyCode],                //企业地址list = checkData.creditCode
                    that.ruleCompanyAuth.selecteCity = province + '/' + city + '/' + county
                    that.provinceVal = checkData.provinceCode
                    that.cityVal = checkData.cityCode
                    that.countyVal = checkData.countyCode
                    that.selecteCityShowLenght = that.selecteCity;
                    that.disabled.selecteCity = true;  
                }else {
                    that.selecteCityShowLenght = []
                    that.selecteCity = []
                    that.ruleCompanyAuth.selecteCity = ''
                    that.disabled.selecteCity = false; 
                }
                if ([null, undefined, ''].indexOf(checkData.address) == -1) {  // 详细地址
                    that.ruleCompanyAuth.companyAddress = checkData.address
                    that.disabled.companyAddress = true;
                }else {
                    that.ruleCompanyAuth.companyAddress = ''
                    that.disabled.companyAddress = false;
