        uploadImgPhone (e, num) { //点击本地图片
                var that = this ; 
                if(that.ruleCompanyAuth.liveActionsImg.length >4) {
                     Notify({
						message: "企业实景图最多五张",
						duration: 2000,
						background: '#E6A23C'
					});
                    return false
                }
                that.imgValue = "";     
                that.inputImgValue = e.target.id;
                var file = e.target.files[0]   
                that.filecCoverImgType = file.name.split('.')[1]
                if(num != 2) {
                    if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) {                
                        // alert('图片类型必须是.gif,jpeg,jpg,png,bmp中的一种')   
                         MessageBox.alert('', { 
                            message: value, 
                         	title: '图片类型必须是.gif,jpeg,jpg,png,bmp中的一种', 
                         	confirmButtonText: '确定',
                         }).then((action) => { 
                            
                         }).catch(error => { 
                             
                         });                          
                        return false            
                    };
                }else {
                    if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(e.target.value)) { // 一手招聘订单非图片    
						if(that.ruleCompanyAuth.firstOrder.length > 0){
							that.$message({
								message: "不能同时上传文件和图片",
								type: "warning",
								duration: 3000
							});
							return false;
						}    
					}else {   // 图片类型
						if(that.firstOrderFile.length > 0){
							that.$message({
								message: "不能同时上传文件和图片",
								type: "warning",
								duration: 3000
							});
							return false;
						}
					}

                    // 判断是否为IE浏览器： /msie/i.test(navigator.userAgent) 为一个简单正则
                    var isIE = /msie/i.test(navigator.userAgent) && !window.opera;
                        var fileSize = 0;
                        if (isIE && !e.target.files) {    // IE浏览器
                            var filePath = e.target.value; // 获得上传文件的绝对路径
                            /**
                             * ActiveXObject 对象为IE和Opera所兼容的JS对象
                             * 用法：
                             *         var newObj = new ActiveXObject( servername.typename[, location])
                             *         其中newObj是必选项。返回 ActiveXObject对象 的变量名。
                             *        servername是必选项。提供该对象的应用程序的名称。
                             *        typename是必选项。要创建的对象的类型或类。
                             *        location是可选项。创建该对象的网络服务器的名称。
                             *\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\
                            *     Scripting.FileSystemObject 为 IIS 内置组件，用于操作磁盘、文件夹或文本文件，
                            *    其中返回的 newObj 方法和属性非常的多
                            *    如：var file = newObj.CreateTextFile("C:\test.txt", true) 第二个参表示目标文件存在时是否覆盖
                            *    file.Write("写入内容");    file.Close();
                            */
                            var fileSystem = new ActiveXObject("Scripting.FileSystemObject");
                            // GetFile(path) 方法从磁盘获取一个文件并返回。
                            var file = fileSystem.GetFile(filePath);
                            fileSize = file.Size;    // 文件大小，单位：b
                        }
                        else {    // 非IE浏览器
                            fileSize = e.target.files[0].size;
                        }
                        var size = fileSize / 1024 / 1024;
                        if (size > 10) {
                            alert("与客户的合作协议不能大于10M");
                            that.imgValue = ''
                            return false 
                        }
                }        
                    var reader = new FileReader()            
                    reader.onload = (e) => {                
                        let data                
                        if (typeof e.target.result === 'object') { // 把Array Buffer转化为blob 如果是base64不需要                    
                            data = window.URL.createObjectURL(new Blob([e.target.result]))                
                        } else {                    
                            data = e.target.result        
                        }                
                        if (num === 1) {                    
                            that.option.img = data                
                        }     
                        if(that.inputImgValue == "companyLogoId"){
                            that.option.fixedNumber = [1, 1];
                        }else {
                            that.option.fixedNumber = [4, 3];
                        }   
                        if(that.inputImgValue == "businessLicenseImgId" || that.inputImgValue == "orderId") {
                            that.loading = true;
                            that.showCutImg = false;
                            if(that.inputImgValue == "businessLicenseImgId") {
                                that.imgModuleName = 'businessLicenseImg'   // 营业执照模块
                            }else if(that.inputImgValue == 'orderId') {
                                that.imgModuleName = 'recruitmentOrder'   // 一手招聘订单 模块
                            }
                            if (data.length > 0) {  //判断是使用原图还是裁剪图
                                var arr = data.split(','),     //方法用于把cropImage分割成字符串数组。
                                    mime = arr[0].match(/:(.*?);/)[1],
                                    bstr = atob(arr[1]),            // 去掉url的头，并转化为byte(解码)
                                    n = bstr.length,                // 处理异常,将ascii码小于0的转换为大于0
                                    u8arr = new Uint8Array(n);      // 生成视图（直接针对内存）：8 位无符号整数值的类型化数组，长度1个字节
                                while(n--){
                                    u8arr[n] = bstr.charCodeAt(n);    //方法可返回指定位置的字符的 Unicode 编码
                                }
                            }
                            var obj = new Blob([u8arr], {type:mime}); 
                            var OSS = require('ali-oss');
                            var client = new OSS({
                                region : "oss-cn-hangzhou",
                                accessKeyId: that.oss.accessKeyId,
                                accessKeySecret: that.oss.accessKeySecret,
                                stsToken: that.oss.securityToken,
                                bucket: "ylsite"
                            });
                            var date = new Date();
                            var toLocaleDateString = date.toLocaleDateString()
                            var timestamp = Date.parse(new Date());
                        async function multipartUpload() {
                            try {
                                let resultLicense = await client.multipartUpload('oms/' + that.imgModuleName + '/' + toLocaleDateString + '/' + that.systemUserId + '_' + timestamp + '.' + that.filecCoverImgType, obj);
                                var ossSrc = '';
                                if(that.inputImgValue == "orderId") {
                                    if(resultLicense.res.statusCode) {
                                         Notify({
                                            message: '上传成功!',
                                            duration: 1500,
                                            background: '#20D3B3'
                                        });
                                    }
                                    if ('gif|jpg|jpeg|png|bmp|GIF|JPG|PNG'.indexOf(that.filecCoverImgType) == -1) {    // 一手订单 非图片类型             
                                        if(resultLicense.res.requestUrls[0].indexOf('?') !== -1){
                                            var ossArr = resultLicense.res.requestUrls[0].split('?')
                                            that.firstOrderFile = ossArr[0]
                                            that.showFile = true;
                                        }else{
                                            ossSrc = resultLicense.res.requestUrls[0];
                                            that.firstOrderFile = ossSrc
                                            that.showFile = true;
                                        }          
                                    }else {   // 一手订单 是图片类型   
                                         if(resultLicense.res.requestUrls[0].indexOf('?') !== -1){
                                            var ossArr = resultLicense.res.requestUrls[0].split('?')
                                            that.ruleCompanyAuth.firstOrder.push((ossArr[0]))
                                        }else{
                                            ossSrc = resultLicense.res.requestUrls[0];
                                            that.ruleCompanyAuth.firstOrder.push(ossSrc)
                                        }          
                                    }  
                                    that.loading = false;
                                }else {
                                    if(resultLicense.res.requestUrls[0].indexOf('?') !== -1){
                                        var ossArr = resultLicense.res.requestUrls[0].split('?')
                                        ossSrc = ossArr[0]
                                    }else{
                                        ossSrc = resultLicense.res.requestUrls[0];
                                    }
                                    that.ruleCompanyAuth.businessLicenseImg = ossSrc;       // 营业执照模块
                                    that.businessLicenseImg.push(ossSrc)
                                }
                                that.loading = false;
                            }catch(e) {
                                // 捕获超时异常
                                that.loading = false;
                                Notify({
                                        message: '图片上传异常',
                                        duration: 2000,
                                        background: '#FF6262'
                                    });
                                that.getOssParams()
                                if(e.code === 'ConnectionTimeoutError') {
                                    console.log("Woops,超时啦!");
                                }
                            }
                        }
                        multipartUpload()
                        }else {
                            that.showCutImg = true; 
                        }

                    } // 转化为base64            
                    reader.readAsDataURL(file) // 转化为blob reader.readAsArrayBuffer(file)        
        }, 
