                                            that.ruleCompanyAuth.companyLogo = result.res.requestUrls[0];
                                            that.logoImgVModel = result.res.requestUrls[0];
                                            that.toggleCompanyLogo = false;
                                            that.loading = false;
                                        }
                                        if(that.inputImgValue == 'surfacePlotImgId') {
                                            that.ruleCompanyAuth.surfacePlotImg = result.res.requestUrls[0];
                                            that.surfacePlotVModel = result.res.requestUrls[0];
                                            that.toggleSurfacePlotImg = false;
                                            that.loading = false;
                                        }
                                        if(that.inputImgValue == 'liveActionsImgId') {
                                            that.ruleCompanyAuth.liveActionsImg.push(result.res.requestUrls[0])
                                            that.loading = false;
                                        }
                                    }
                                }catch(e) {
                                    that.loading = false
                                    Notify({
                                        message: '图片上传异常',
                                        duration: 2000,
                                        background: '#FF6262'
                                    });
                                    that.getOssParams()
                                // 捕获超时异常
                                    if(e.code === 'ConnectionTimeoutError') {
                                        console.log("Woops,超时啦!");
                                    }
                                }
                            }
                            multipartUpload()
                        })
                    }
                    that.showCutImg = false;
        },
            
        falseCutImg() {     // 取消裁剪
            this.showCutImg = false;  
        },
        getOssParams() {         // 图片信息 获取OSS参数
                var that = this;
                var token = Util.getCookie("qdsMToken"); 
                that.systemUserId = localStorage.getItem('qdsSCorpUserId')      //用户id
                that.systemUserId = 12749
                that.axios(that.ossGlobalApiUrl + 'oss/assumeRole?token=' + token, {
                    method: 'post',
                    params: { 
                        sysUserId: that.systemUserId,       // 用户id
                    },
                    timeout:10000
                })
                .then((response)=>{
                    if(response.data.code == 10000){
                        that.oss.accessKeyId = response.data.data.credentials.accessKeyId
                        that.oss.accessKeySecret = response.data.data.credentials.accessKeySecret
                        that.oss.securityToken = response.data.data.credentials.securityToken
                    }
                    

                }).catch((error)=>{
                    
                })
        },
        recertification() {    // 重新认证 信息回显
                var that = this;
				var token = Util.getCookie('qdsMToken'); 
                that.axios(that.qdsGlobalApiUrl +"ylw/qhChannelCorp/api/getChannelInfoByUserId?token=" + token,   // that.qdsGlobalApiUrl  "http://10.0.15.58:19000/"
                    {
                        method: "get",
                    }
                ).then(function(response) {
                    if(response.data.code == 10000) {
                        var checkData = response.data.data.corpInfo
                        that.provinceVal = checkData.provinceCode   // 省code
                        that.cityVal = checkData.cityCode          // 市code
                        that.countyVal = checkData.countyCode      // 区code
                        var province = DLArea.getName(checkData.provinceCode)
                        var city = DLArea.getName(checkData.cityCode)
                        var county = DLArea.getName(checkData.countyCode)
                        that.selecteCity = [that.provinceVal, that.cityVal, that.countyVal]  //企业地址
                        that.selecteCityShowLenght = that.selecteCity;
                        var companysSize = ''
                        var companysNature = ''
                        var companyIndustry = ''
                        DLSizeList.forEach((item, index) =>{    // 企业规模
                            if(item.code == checkData.enterpriseScaleCode) {
                                companysSize = item.name
                                that.chanceScaleCode = checkData.enterpriseScaleCode
                            }
                        }) 
                        DLNatureList.forEach((item, index) =>{   // 企业性质
                            if(item.code == checkData.companyNatureCode) {
                                companysNature = item.value
                                that.chanceNatureCode = checkData.companyNatureCode
                            }
                        })
                        DLTypeList.forEach((item, index) =>{
                            if(item.code == checkData.industryCode) {
                                companyIndustry = item.name
                                that.chanceIndustryCode = checkData.industryCode
                            }
                        })
                        var  img = response.data.data.photos

                        if ([null, undefined, ''].indexOf(img.attachmentUrl) == -1) {         // 一手招聘订单 是否有值
                            var arr = img.attachmentUrl.split('.')
                            var index = arr.length -1
                            var type = arr[index]
                            if ('gif|jpg|jpeg|png|bmp|GIF|JPG|PNG'.indexOf(type) == -1) {    // 一手订单 非图片类型             
                                    that.firstOrderFile = img.attachmentUrl
                                    that.showFile = true
                                    var firstOrderStr = []
                            }else {                                                          // 一手订单 是图片类型   
                                if(img.attachmentUrl.indexOf(',') !== -1){   // 多张图片
                                    var firstOrderStr = img.attachmentUrl.split(',')
								}else{   // 单张图片
									var firstOrderStr = [img.attachmentUrl]
								}         
                            }
                        } 
                        that.businessLicenseImg =[img.businessLicenceUrl]
                        that.ruleCompanyAuth = {
                            id: checkData.id,         // 重新认证id
                            corpCode: checkData.corpCode,
                            companyAllName: checkData.corpName,   // 企业全称
                            companyTitle: checkData.shortName,     // 企业简称
                            companyCode: checkData.creditCode,      // 统一社会信用代码 
                            selecteCity: province + '/' + city + '/' + county,   // 企业地址
                            companyAddress:checkData.address,    // 详细地址
                            companysSize:companysSize,      // 企业规模
                            companysNature:companysNature,    // 企业性质
                            companyIndustry: companyIndustry,   // 所属行业
                            companyWorkCont:checkData.businessScope,
                            businessLicenseImg: img.businessLicenceUrl,
                            companyLogo: img.logoUrl,
                            surfacePlotImg:img.coverUrl,
                            liveActionsImg: img.environmentUrls,
                            firstOrder: firstOrderStr       //   一手订单/与客户合作协议图片
                        }
                    }else {
                        Notify({
                            message: response.data.message,
                            duration: 3000,
                            background: '#E6A23C'
                        });
                    }
                    
                })
                .catch(function(error) {
                    
                });
        },
 	}
}
</script>

